.root { 
    $height: 42vw;

    margin: 0;
    padding: 0;
    width: 100%; 
    box-sizing: border-box;
    padding: 0 10vw;
    background-size: 100% auto;
    display: flex;
    flex-wrap: wrap;
    height: $height;

    .content {
        width: 100%; 
        display: flex;
        flex-wrap: wrap;

        .photo {
            width: 43.5%;
            height: 100%;

            img {
                max-width: none;
                height: $height;
                width: 33.0vw;
                padding: 1vw 0 0 0.7vw;
                box-sizing: border-box;
            }
        }

        .text {
            width: 50%;
            padding-top: 10vw;
            font-size: 1.3vw;
            line-height: 1.4vw;

            p {
                margin: 0;
                margin-top: 1.2vw;
            }
        }
    }
}


