@import 'variables.scss';
@import url('https://fonts.googleapis.com/css?family=Nova+Flat');

.root {
  z-index: 1000;

  header {
    text-align: right;

    h2 {
      text-transform: uppercase;
      font-family: 'Nova Flat', cursive;
    }
  }

  .stepline {
    padding-bottom: 3vw;

    img {
      max-width: 100%;
    }
  }

  input[type=text], select {
    border: 0;
    background: transparent;
    border-bottom: 0.2vw $grey-1 solid;
  }

  .form-wrapper {
    padding-right: 3vw;
  }

  .continue {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    span {
      display: block;
      width: 100%;
      text-align: center;
      padding-bottom: 1vw;
      font-size: 1.4vw;
    }

    button[type=submit] {
      display: flex;
      height: 3vw;
      width: 19.5vw;
      background: $green;
      border-radius: 999px;
      justify-content: center;
      text-align: center;
      outline: none;
      border: none;
      color: #ffffff;
      font-size: 1.4vw;
      cursor: pointer;
    }
  }

  .vote {
    justify-content: center;

    & > div {
      justify-content: center;
      display: flex;
    }

    ul {
      width: 100%;
      justify-content: center;
      list-style: none;
      display: flex;
      text-align: center;
      padding: 0;

      li {
        cursor: pointer;
        margin: 0 0.7vw;

        &:not(.active) {
          filter: grayscale(100%);
        }
      }
    }
  }

  .step {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  
  .step .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;

    & > div:not(.continue), & > label {
      width: 100%;
      padding-bottom: 2.2vw;

      display: flex;
      justify-content: space-between;
    }

    .four-weak {
      display: flex;
      justify-content: flex-start;
      flex-wrap: wrap;

      div {
        width: 100%;
        display: flex;
        justify-content: center;
        padding: 0.5vw 0;

        label:first-child {
          width: 30%;
        }
        label:last-child {
          width: 35%;
        }
      }
    }
  }

  .step0 .content {
    width: 31%;

    select {
      color: $textColor;
      user-select: none;
    }
    
    input[type=text], select {
      width: 100%;
    }

    .double { input[type=text], select {
      width: 45%;
      &:first-child {
        margin-right: 5%;
      }
      &:last-child {
        margin-left: 5%;
      }
    }}

    & > div {
      width: 100%;
      padding-bottom: 1.2vw;
    }
  }

  .step1 .content {
    width: 60%;   

    & > div:not(.continue) {
      width: 100%;
      padding-left: 15%;
      padding-bottom: 2.2vw;
    }
  }

  .step2 .content {
    width: 80%;
    
    .check {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 2vw 4vw 2vw 8vw;

      header {
        width: 100%;
        padding: 1vw 0;
        text-align: left;
      }

      label {
        display: flex;
        width: 33%;
      }
    }

    & > div:not(.continue) {
      width: 100%;
      padding-left: 15%;
      padding-bottom: 2.2vw;
    }
  }

  .step3 .content {
    width: 75%;
  }

  .step4 .content {
    
  }
}


