@import 'variables.scss';
@import url('https://fonts.googleapis.com/css?family=Dancing+Script');
@import url('https://fonts.googleapis.com/css?family=Oswald');
@import url('https://fonts.googleapis.com/css?family=Barlow+Condensed');

.root { 
    margin: 0;
    padding: 0;
    width: 100%; 
    color: #ffffff; 
    background: #419be0;
    position: relative;
    overflow-x: hidden;

    %centered-absolue-el {
        width: 100%;
        display: flex;
        position: absolute;
        justify-content: center;
        z-index: 100;
    }

    .motivator {

        $outlineStrong: 0.1vw;

        @extend %centered-absolue-el;
        font-family: 'Barlow Condensed', sans-serif;
        font-weight: 600;
        top: 22.8vw;
        transform: scaleX(0.83);
        font-size: 5.8vw;
        text-shadow: 
		-0   -1px 0   rgba(36, 36, 36, 0.2),
		 0   -1px 0   rgba(36, 36, 36, 0.2),
		-0    1px 0   rgba(36, 36, 36, 0.2),
		 0    1px 0   rgba(36, 36, 36, 0.2),
		-1px -0   0   rgba(36, 36, 36, 0.2),
		 1px -0   0   rgba(36, 36, 36, 0.2),
		-1px  0   0   rgba(36, 36, 36, 0.2),
		 1px  0   0   rgba(36, 36, 36, 0.2),
		-1px -1px 0   rgba(36, 36, 36, 0.2),
		 1px -1px 0   rgba(36, 36, 36, 0.2),
		-1px  1px 0   rgba(36, 36, 36, 0.2),
		 1px  1px 0   rgba(36, 36, 36, 0.2),
		-1px -1px 0   rgba(36, 36, 36, 0.2),
		 1px -1px 0   rgba(36, 36, 36, 0.2),
		-1px  1px 0   rgba(36, 36, 36, 0.2),
		 1px  1px 0   rgba(36, 36, 36, 0.2);
        //font-weight: 500;
    }

    .actor {
        @extend %centered-absolue-el;
        top: 30vw;
        font-size: 12vw;
        font-family: 'Dancing Script', cursive;
        text-shadow: 0px 0px 2px rgba(150, 150, 150, 1);

        span::first-letter {
            color: $red;
        }
    }

    .action-btn {
        @extend %centered-absolue-el;
        top: 48vw;

        button {
            top: 7vw;
            background-color: #86e484;
            color: #ffffff;
            border-radius: 2vw;
            height: 4vw;
            font-size: 1.3vw;
            font-weight: 600;
            display: flex;
            align-items: center;
            padding: 0 2vw;
            outline: none;
            border: none;
            cursor: pointer;
        }
    }

    .contacts {
        position: absolute;
        height: 100%;
        right: 4vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        z-index: 100;
        box-sizing: border-box;
        padding-bottom: 6vw;

        a {
            width: 2.70vw;
            height: 2.70vw;
            margin: 2vw 0;

            &:hover img {
                filter: invert(20%);
            }

            img {
                max-width: 100%;
                max-height: 100%;
            }
        }
    }

    .slide {
        width: 100vw;
        height: 60vw;

        img {
            width: 100%;
        }
    }

    .dots {
        @extend %centered-absolue-el;
        display: flex !important; //slisk inline style display: block
        flex-wrap: wrap;
        list-style: none;
        bottom: 2vw;
        justify-content: center;
        padding: 0;
        margin: 0;

        li {
            padding: 0 1.3vw;

            button {
                width: 0.8vw;
                height: 0.8vw;
                border: 1px solid #ffffff;
                border-radius: 50%;
                background: transparent;
                cursor: pointer;
                user-select: none;
                box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
            }
        }

        [class="slick-active"] button {
            background: #ffffff;
        }
    }
}


