.root { 
    width: 100%; 
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap; 
    height: 35vw;
    padding-top: 7vw;

    &.active {
        height: 40vw;

        * {
            transition: all 0.3s;
        }

        .title img {
            width: 2.88vw;
            height: 22.5vw;
            top: 0.7vw;
        }

        .photo {
            width: 27.5%;

            img {
                width: 12.2vw;
                height: 24.8vw;
            
                &.after {
                    filter: blur(0);
                }
            }
        }

        .text {
            width: 40%;

            span {
                display: inline;
            }

            button {
                display: none;
            }
        }
    }

    img {
        height: 100%;
        max-width: none;
    }
    
    .title {
        width: 19.5%; 
        position: relative;
        img {
            position: absolute;

            width: 2.88vw * 0.7;
            height: 22.5vw * 0.7;
            top: -1vw;
            right: 5.9vw;
        }
    }

    .photo {
        width: 19.2%;
        display: flex;

        img {
            width: 6.8vw;
            height: 14vw;
            padding-right: 0.45vw;

            &.after {
                filter: blur(0.2vw);
            }
        }
    }

    .text {
        width: 40%;
        font-size: 0.8vw;
        line-height: 1.5vw;

        b {
            font-size: 1.8vw;
            display: block;
        }

        span {
            display: none;
        }

        button {
            background: transparent;
            outline: none;
            font-style: italic;
            font-weight: 600;
            border: none;
            display: block;
            padding-top: 1vw;
            font-size: 1.3vw;
            padding-left: 0;
            cursor: pointer;

            &:hover {
                filter: brightness(1.8)
            }
        }
    }
    
}


