@import url("https://fonts.googleapis.com/css?family=Nova+Flat");

.root {
  z-index: 1000;
}

.root header {
  text-align: right;
}

.root header h2 {
  text-transform: uppercase;
  font-family: 'Nova Flat', cursive;
}

.root .stepline {
  padding-bottom: 3vw;
}

.root .stepline img {
  max-width: 100%;
}

.root input[type=text],
.root select {
  border: 0;
  background: transparent;
  border-bottom: 0.2vw #aeaeae solid;
}

.root .form-wrapper {
  padding-right: 3vw;
}

.root .continue {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.root .continue span {
  display: block;
  width: 100%;
  text-align: center;
  padding-bottom: 1vw;
  font-size: 1.4vw;
}

.root .continue button[type=submit] {
  display: flex;
  height: 3vw;
  width: 19.5vw;
  background: #86e484;
  border-radius: 999px;
  justify-content: center;
  text-align: center;
  outline: none;
  border: none;
  color: #ffffff;
  font-size: 1.4vw;
  cursor: pointer;
}

.root .vote {
  justify-content: center;
}

.root .vote > div {
  justify-content: center;
  display: flex;
}

.root .vote ul {
  width: 100%;
  justify-content: center;
  list-style: none;
  display: flex;
  text-align: center;
  padding: 0;
}

.root .vote ul li {
  cursor: pointer;
  margin: 0 0.7vw;
}

.root .vote ul li:not(.active) {
  filter: grayscale(100%);
}

.root .step {
  width: 100%;
  display: flex;
  justify-content: center;
}

.root .step .content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.root .step .content > div:not(.continue),
.root .step .content > label {
  width: 100%;
  padding-bottom: 2.2vw;
  display: flex;
  justify-content: space-between;
}

.root .step .content .four-weak {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.root .step .content .four-weak div {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0.5vw 0;
}

.root .step .content .four-weak div label:first-child {
  width: 30%;
}

.root .step .content .four-weak div label:last-child {
  width: 35%;
}

.root .step0 .content {
  width: 31%;
}

.root .step0 .content select {
  color: #242424;
  user-select: none;
}

.root .step0 .content input[type=text],
.root .step0 .content select {
  width: 100%;
}

.root .step0 .content .double input[type=text],
.root .step0 .content .double select {
  width: 45%;
}

.root .step0 .content .double input[type=text]:first-child,
.root .step0 .content .double select:first-child {
  margin-right: 5%;
}

.root .step0 .content .double input[type=text]:last-child,
.root .step0 .content .double select:last-child {
  margin-left: 5%;
}

.root .step0 .content > div {
  width: 100%;
  padding-bottom: 1.2vw;
}

.root .step1 .content {
  width: 60%;
}

.root .step1 .content > div:not(.continue) {
  width: 100%;
  padding-left: 15%;
  padding-bottom: 2.2vw;
}

.root .step2 .content {
  width: 80%;
}

.root .step2 .content .check {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 2vw 4vw 2vw 8vw;
}

.root .step2 .content .check header {
  width: 100%;
  padding: 1vw 0;
  text-align: left;
}

.root .step2 .content .check label {
  display: flex;
  width: 33%;
}

.root .step2 .content > div:not(.continue) {
  width: 100%;
  padding-left: 15%;
  padding-bottom: 2.2vw;
}

.root .step3 .content {
  width: 75%;
}




