.root { 
    margin: 0;
    padding: 0;
    width: 100%; 
    color: #ffffff; 
    position: relative;
    display: flex;
    flex-wrap: wrap;
    background-color: green;
    height: 43.5vw;

    .title {
        //display: flex;
        display: none;
        width: 100%;
        position: absolute;
        justify-content: center;
        text-align: center;
        z-index: 100;
        top: 10.5vw;
        font-size: 4.2vw;
        font-weight: 600;
    }

    .form {
        input {
            position: absolute;
            background: transparent;
            padding: 1vw 2vw;
            outline: none;
            border: none;
            right: 16vw;
            width: 24vw;

            &:not(:focus) {
                font-style: italic;
            }
            &[name=name] {
                top: 13.25vw;
            }

            &[name=phone] {
                top: 18.45vw;  
            }

            &[name=email] {
                top: 23.65vw;    
            }
        }

        button {
            color: #ffffff;
            background: transparent;
            outline: none;
            border: none;
            padding: 0;
            position: absolute;
            top: 30.7vw;
            right: 25.2vw;
            width: 10vw;
            height: 3vw;
            font-size: 1.4vw;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 999px;

            &:hover {
                box-shadow: inset 10px 10px 300px 0px rgba(255,255,255,0.2);
                box-shadow: 2px 10px 300px 0px rgba(255,255,255,0.2);
                cursor: pointer;
            }
        }
    }

    img {
        width: 100%; 
        height: 100%;
    }
}


