.root { 
    width: 50vw;
    height: 28.4vw;
    position: relative;
    background-color: #000000;
    display: flex;

    .bg {
        position: absolute;
        background-size: 100% auto;
        width: 100%;
        height: 100%;
        opacity: 0.6
    }

    %item-sections {
        width: 100%;
        display: flex;
        position: absolute;
        justify-content: center;
        text-align: center;
    }

    .titleOne {
        @extend %item-sections;
        top: 3.6vw;
        font-size: 2.2vw;
        font-weight: 600;
    }

    .titleTwo {
        @extend %item-sections;
        top: 11.6vw;
        font-size: 2.8vw;
        font-weight: 600;
        color: #e71d36;
    }

    .action {
        @extend %item-sections;
        top: 22.6vw;
        button {
            font-size: 1.1vw;
            border: 0.15vw #ffffff solid;
            padding: 1.2vw 2.5vw;
            border-radius: 9999px;
            background: transparent;
            outline: none;
            color: #ffffff;
        }
    }
}


