.root { 
    margin: 0;
    padding: 5.5vw 15.8vw 5.6vw 10.4vw;
    width: 100%; 
    box-sizing: border-box;
    background-size: 100% auto;
    height: 42vw;
    position: relative;

    img {
        width: 100%; 
        height: 100%;
    }


    &:hover {
        iframe {
            display: block;
        }
    }
    iframe {
        width: 68vw;
        height: 31vw;
        top: 5.5vw;
        left: 16.05vw;
        position: absolute;
        display: none;
    }
}


