.root { 
    margin: 0;
    padding: 0;
    width: 100%; 
    color: #ffffff; 
    position: relative;
    display: flex;
    flex-wrap: wrap;
    background-color: green;
    height: 35vw;

    .title {
        //display: flex;
        display: none;
        width: 100%;
        position: absolute;
        justify-content: center;
        text-align: center;
        z-index: 100;
        top: 10.5vw;
        font-size: 4.2vw;
        font-weight: 600;
    }

    img {
        width: 100%; 
        height: 100%;
    }
}


